/** @format */
import React, {useState} from 'react'
import 'bootstrap/dist/css/bootstrap.css'
import './App.css'

function App() {
  const [data, setData] = useState('')
  const calcBtns = []
  const digits = [9, 8, 7, 6, 5, 4, 3, 2, 1, 0, '.', '%']
  digits.forEach(item => {
    calcBtns.push(
      <button
        onClick={e => {
          setData(data + e.target.value)
        }}
        value={item}
        key={item}>
        {item}
      </button>,
    )
  })
  return (
    <div className="wrapper">
      <div className="show-input">{data}</div>
      <div className="digits flex">{calcBtns}</div>
      <div className="modifiers subgrid">
        <button onClick={() => setData(data.substr(0, data.length - 1))}>Back</button>
        <button onClick={() => setData('')}>AC</button>
      </div>

      <div className="operations subgrid">
        <button onClick={e => setData(data + e.target.value)} value="+">
          +
        </button>
        <button onClick={e => setData(data + e.target.value)} value="-">
          -
        </button>
        <button onClick={e => setData(data + e.target.value)} value="*">
          *
        </button>
        <button onClick={e => setData(data + e.target.value)} value="/">
          /
        </button>
        <button
          onClick={e => {
            try {
              setData(
                String(eval(data)).length > 3 && String(eval(data)).includes('.')
                  ? String(eval(data).toFixed(4))
                  : String(eval(data)),
              )
            } catch (err) {
              console.log(err)
            }
          }}
          value="=">
          =
        </button>
      </div>
    </div>
  )
}

export default App

